import { mapGetters, mapActions } from 'vuex'
import MusicList from '@/components/Music-List/Music-List'
import Scroll from '@/base/scroll/scroll'
import loading from '@/base/loading/loading'
import list from '@/base/list/list'
import { playlistMixin } from '@/common/js/mixin'
export default {
  mixins: [playlistMixin],
  props: {
    rankList: {}
  },
  data () {
    return {
      songs: []
    }
  },
  methods: {
    ...mapActions(['randomPlay', 'selectPlay']),
    goBack () {
      this.$router.back()
    },
    _setHeight () {
      this.imgHeight = this.$refs['bg-img'].clientHeight
      this.listHeight =
        document.documentElement.clientHeight -
        this.$refs['bg-img'].clientHeight
      this.$refs['list'].style.height = `${this.listHeight}px`
    },
    randomPlayAll () {
      this.randomPlay({list: this.topList})
    },
    select (song, index) {
      let self = this
      this.selectPlay({
        list: self.topList,
        index
      })
    },
    handlePlaylist () {
      let bottom
      this.playlist.length > 0 ? bottom = '60px' : bottom = ''
      // setTimeout(() => {
      //   this.$refs['list'].style.paddingBottom = bottom
      //   this.$refs.scroll.refresh()
      // }, 500)
      this.$nextTick(() => {
        this.$refs['list'].style.paddingBottom = bottom
        this.$refs.scroll.refresh()
      })
    }
  },
  computed: {
    ...mapGetters(['topList']),
    bgImg () {
      return `background: url('${this.rankList.picUrl}') no-repeat;
              backgroundSize: 100% 100%`
    }
  },
  mounted () {
    if (!this.topList.length) {
      this.$router.back()
      return false
    } else {
      this._setHeight()
    }
  },
  components: {
    MusicList,
    Scroll,
    loading,
    list
  }
}



